<template>
  <div class="login">
    <div class="login-header">人信官网管理系统</div>
    <div class="login-box">
      <el-form
        :model="loginLable"
        ref="loginLable"
        label-width="90px"
        class="login-form"
        :label-position="left"
        :rules="rules"
      >
        <el-form-item prop="username" label="用户名:">
          <el-input v-model="loginLable.username"></el-input>
        </el-form-item>

        <el-form-item prop="password" label="密码:">
          <el-input placeholder="请输入密码" v-model="loginLable.password" show-password></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="login">提交</el-button>
      <el-button type="primary" @click="resetForm">重置</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  components: {},
  data() {
    return {
      loginLable: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      },
      left: "left"
    };
  },
  methods: {
    resetForm() {
      this.loginLable = {
        username: "",
        password: ""
      };
    },

    login() {
      this.$refs.loginLable.validate(valid => {
        if (valid) {
          let obj = this.loginLable;
          this.$ajax.post(this.RespondURL.url + "Login", obj).then(data => {
            if (data.data.flag) {
              this.$router.push("/product");
            } else {
              this.$alert("账号或者密码错误，请重新登录", "登录", {
                confirmButtonText: "确定",
                callback: () => {}
              });
            }
          });
        } else {
          this.$alert("账号或者密码错误，请重新登录", "登录", {
            confirmButtonText: "确定",
            callback: () => {}
          });
        }
      });
    }
  }
};
</script>
